@import url(https://fonts.googleapis.com/css2?family=Maven+Pro&family=Public+Sans&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
/* import link of Google fonts */
/*Custom root values for fonts */
:root {
  --heading-font: "Maven Pro", sans-serif;
  --page-font: "Public Sans", sans-serif;
}

/*Selection of fonts for all neccessary elements */
.todo-container,
.btns,
#input-text {
  font-family: "Public Sans", sans-serif;
  font-family: var(--page-font);
}

.main-container {
  width: 100%;
  height: 100vh;
  background: url("/images/todo.jpg") no-repeat;
  background-size: cover;
}

.heading {
  font-size: 2.5rem;
  font-family: "Maven Pro", sans-serif;
  font-family: var(--heading-font);
  text-align: center;
  padding: 3rem 0 1rem 0;
}

.todo-container {
  width: 25%;
  /* border: 2px solid red; */
  max-width: 35%;
  min-width: 290px;
  height: auto;
  padding: 15px;
  background: #ffffffe8;
  border-radius: 11px;
  margin: 2rem auto;
  box-shadow: 0 0 20px -4px #151515;
  transition: all 3s cubic-bezier(0.19, 1, 0.22, 1);
}

.todo-container:hover {
  box-shadow: 0 0 20px -4px #4654b1;
  -webkit-transform: scale(1.05, 1.05);
          transform: scale(1.05, 1.05);
}

.todo-head {
  margin: 2em 0;
  padding: 0 10px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  /* border: 2px solid purple; */
}

#input-text {
  height: 28px;
  font-size: 1em;
  width: 80%;
  padding: 0 4px;
  outline: none;
}

.btns {
  margin: 0 5px;
  font-size: 1.3em;
  height: 28px;
  width: 50px;
}

.body-heading {
  letter-spacing: 1px;
  color: #4c3baa;
  /* border: 2px solid purple; */
}

.todo-body {
  /* border: 2px solid purple; */
  margin: 1em 0;
}

.btns:hover {
  cursor: pointer;
}

/*Media query only for Samsung fold devices */
@media screen and (max-width: 299px) {
  .todo-container {
    min-width: 270px;
  }
}

.task-box {
  padding: 4px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 10px 0;
  border: 2px solid #3ebd97a8;
  background: linear-gradient(17deg, #50bfcd61, transparent);
  border-radius: 4px;
}

#taskData {
  /* border: 2px solid deeppink; */
  width: 70%;
  font-size: 1.1em;
  line-break: anywhere;
}

.task-btns {
  /* border: 2px solid orange; */
  width: 25%;
  display: flex;
  justify-content: center;
  align-items: center;
}

#deleteBtn,
#doneBtn {
  margin: 0;
}

